  async function handleOpenAINode(nodeData: any) {
    try {
      const { showParams, params, prompt, configId } = nodeData?.openAiData;

      const checkedItems = {};
      if (showParams) {
        params?.forEach((item) => {
          if (item.isCheck) {
            checkedItems[item.name] = item.value;
          } else {
            checkedItems[item.name] = item.defaultValue;
          }
        });
      }

      const dataToSend = {
        configId: configId,
        prompt: variableParser(prompt, variableData),
        showParams: showParams,
        ...checkedItems,
      };
      await initiateChatLoader();
      const response = await axiosPost("/liveBot/openAiResponse", {
        ...dataToSend,
      });
      const variable = { [nodeData.variableName]: response?.data?.data };

      setVariableData((pre) => ({ ...pre, ...variable }));
    } catch (err) {
      console.log(err);
      const variable = { [nodeData.variableName]: "Something went wrong" };
      setVariableData((pre) => ({ ...pre, ...variable }));
    } finally {
      if (!questions[currentQuestionIndex]?.outputs[0].connections)
        return setIsLast(true);
      const currentIndex = questions.findIndex(
        (x) =>
          x.drawFlowId ===
          questions[currentQuestionIndex]?.outputs[0].connections?.node,
      );
      setCurrentQuestionIndex(currentIndex);
      await removeChatLoader();
    }
  }

  async function handleLinkedIn(nodeData: any) {
    try {
      const parsedData = {};
      for (const key in nodeData.linkedInData) {
        parsedData[key] = variableParser(
          nodeData.linkedInData[key],
          variableData,
          "",
        );
      }
      await initiateChatLoader();
      const response = await axiosPost("/liveBot/linkedIn", parsedData);

      const { profileId, profileData } = response.data.data;
      const linkedInVariables = {};
      const nodeId = nodeData.drawFlowId;

      linkedInVariables[`${nodeId}_profileId`] = profileId;
      if (profileData) {
        linkedInVariables[`${nodeId}_publicId`] = profileData.publicId;
        linkedInVariables[`${nodeId}_firstName`] = profileData.firstName;
        linkedInVariables[`${nodeId}_lastName`] = profileData.lastName;
        linkedInVariables[`${nodeId}_location`] = profileData.location;
        linkedInVariables[`${nodeId}_headline`] = profileData.headline;
        linkedInVariables[`${nodeId}_industry`] = profileData.industry;
        linkedInVariables[`${nodeId}_summary`] = profileData.summary || "";
      }

      setVariableData((pre) => ({ ...pre, ...linkedInVariables }));
    } catch (err) {
      console.log(err);
    } finally {
      if (!questions[currentQuestionIndex]?.outputs[0].connections)
        return setIsLast(true);
      const currentIndex = questions.findIndex(
        (x) =>
          x.drawFlowId ===
          questions[currentQuestionIndex]?.outputs[0].connections?.node,
      );
      setCurrentQuestionIndex(currentIndex);
      await removeChatLoader();
    }
  }

  const evalCondition = (userResponse: any, operator: any, value: any) => {
    switch (operator) {
      case "EQUAL TO":
        return userResponse == value;
      case "NOT EQUAL TO":
        return userResponse != value;
      case "LESS THAN":
        return userResponse < value;
      case "GREATER THAN":
        return userResponse > value;
      case "GREATER THAN EQUAL TO":
        return userResponse >= value;
      case "LES THAN EQUAL TO":
        return userResponse <= value;
      default:
        return false;
    }
  };
