import {
  UserAvatar1,
  UserAvatar2,
  UserAvatar3,
  UserAvatar4,
} from "../../assets/AiBotAssets";
import Markdown from "react-markdown";
import { PrismAsyncLight as SyntaxHighlighter } from "react-syntax-highlighter";
import {
  vscDarkPlus as themeDark,
  vs as themeLight,
} from "react-syntax-highlighter/dist/esm/styles/prism";
import remarkGfm from "remark-gfm";
import rehypeExternalLinks from "rehype-external-links";

function BotComponent(props: any) {
  const {
    currentIndex,
    chatConfig,
    msg,
    botData,
    starterQues,
    activeCat,
    handleStarterQues,
    textColor,
    embed,
    suggestedQuesAns,
    message,
  } = props;
  const typography = chatConfig?.typography;
  const preMadeBotAvatars = [
    "https://dlvkyia8i4zmz.cloudfront.net/EFqzaPhTuSuU22xdqgXg_Layer_1.svg",
    "https://dlvkyia8i4zmz.cloudfront.net/ll8VhXAqSIuthNPmrtQC_Asset_2_1.svg",
    "https://dlvkyia8i4zmz.cloudfront.net/egaBxw0QpewpONdXBhXO_Asset_2_1__1_.svg",
    "https://cdn.filestackcontent.com/6rykG4rRw2W7ccMjRfeJ",
  ];
  const Text = (
    <p
      className={chatConfig?.theme === "dark" ? `dark-font` : `light-font`}
      style={{
        fontSize: typography?.size + "px",
        fontFamily: typography?.font?.fontFamily,
      }}
    >
      {typeof msg.text === "string" ? (
        msg.aiResponse ? (
          <Markdown
            remarkPlugins={[remarkGfm]}
            rehypePlugins={[[rehypeExternalLinks, { target: "_blank" }]]}
            components={{
              code(props) {
                const { children, className, node, ...rest } = props;
                const match = /language-(\w+)/.exec(className || "");

                return match ? (
                  <SyntaxHighlighter
                    children={String(children).replace(/\n$/, "")}
                    style={
                      chatConfig?.theme === "dark" ? themeLight : themeDark
                    }
                    language={match[1]}
                    PreTag="div"
                  />
                ) : (
                  <code {...rest} className={className}>
                    {children}
                  </code>
                );
              },
            }}
          >
            {msg.text}
          </Markdown>
        ) : (
          msg.text
        )
      ) : (
        msg.text.value
      )}
    </p>
  );
  const findMessage = (item) => {
    return item === msg;
  };

  return (
    <>
      <div key={currentIndex} className="botleft">
        <div
          className={`Previewthumbnail ${
            chatConfig?.theme === "dark" ? "dark-bg" : "light-bg"
          } `}
        >
          <img
            src={
              chatConfig?.botAvatar?.isPreMade
                ? preMadeBotAvatars[chatConfig?.botAvatar?.svgId - 1]
                : chatConfig?.botAvatar?.url
            }
            alt="boticon"
          />
        </div>
        <div
          className={`botChatBox ${
            chatConfig?.theme === "dark" ? "dark-bg" : "light-bg"
          } `}
        >
          {msg.name === "button" ? (
            <p
              className={
                chatConfig?.theme === "dark" ? `dark-font` : `light-font`
              }
              style={{
                fontSize: typography?.size + "px",
                fontFamily: typography?.font?.fontFamily,
                fontWeight: typography?.weight,
              }}
            >
              {" "}
              {typeof msg.text === "string" ? msg.text : msg.text.value}
            </p>
          ) : msg?.isLoader ? (
            <div className="preview-loader">
              <div className="dot-flashing"></div>
            </div>
          ) : msg.showImg && msg.image ? (
            <div className="questionButton-answer">
              <img src={msg.image} alt="" />
              {Text}
            </div>
          ) : (
            Text
          )}
        </div>
      </div>
      {/* // SuggestedQuestion Section     */}
      {(botData?.aiStarterQues?.initialMsg ==
        message[message.length - 1]?.text &&
        botData?.aiStarterQues?.initialMsg == msg.text) ||
      (suggestedQuesAns &&
        message.findIndex(findMessage) === message.length - 1 &&
        !message[message.length - 1]?.text?.isMandatory) ? (
        <div className="preview-starter-ques">
          {
            botData && starterQues && starterQues.length > 0
            //  <h4 className={`${chatConfig?.theme === "dark" ? "dark-font" : "light-font"}`} style={{ fontFamily: chatConfig?.typography?.font?.fontFamily }}>SUGGESTED QUESTIONS</h4>
          }
          <ul>
            {botData &&
              starterQues &&
              botData?.aiStarterQues?.initialMsg?.trim().length > 0 &&
              (!activeCat && botData?.aiStarterQues?.isCat
                ? botData?.aiStarterQues?.cats.map((cat) => {
                    return (
                      <li
                        style={{
                          fontSize: chatConfig?.typography?.size + "px",
                          fontFamily: chatConfig?.typography?.font?.fontFamily,
                        }}
                        onClick={() => {
                          handleStarterQues(cat);
                        }}
                      >
                        {cat}
                      </li>
                    );
                  })
                : starterQues?.map((item) => {
                    return (
                      <li
                        style={{
                          fontSize: chatConfig?.typography?.size + "px",
                          fontFamily: chatConfig?.typography?.font?.fontFamily,
                        }}
                        onClick={() => {
                          handleStarterQues(item?.ques);
                        }}
                      >
                        {item?.ques}
                      </li>
                    );
                  }))}
          </ul>
          {activeCat && (
            <UserComponent
              chatConfig={chatConfig}
              msg={activeCat}
              textColor={textColor}
              embed={embed}
            />
          )}
        </div>
      ) : null}
    </>
  );
}
function UserComponent(props: any) {
  const { currentIndex, chatConfig, msg, textColor, embed } = props;
  const typography = chatConfig?.typography;
  const preMadeUserAvatars = [
    <UserAvatar1 />,
    <UserAvatar2 />,
    <UserAvatar3 />,
    <UserAvatar4 />,
  ];

  function getIconByIndex(id: any) {
    return preMadeUserAvatars.find((atr, index) => index === id) || null;
  }
  const hexToRgb = (hex: string) => {
    let pattern_color = "^#([A-Fa-f0-9]{6})$";
    if (hex.match(pattern_color)) {
      let hex_color = hex.replace("#", "");
      let r = parseInt(hex_color.substring(0, 2), 16);
      let g = parseInt(hex_color.substring(2, 4), 16);
      let b = parseInt(hex_color.substring(4, 6), 16);
      return `rgba(${r}, ${g}, ${b}, 0.2)`;
    }
  };

  const Avatar = !chatConfig?.userAvatar?.isPreMade ? (
    <img src={chatConfig?.userAvatar?.url} alt="boticon" />
  ) : (
    <div
      className="userAvatar userAvatarRight"
      style={{
        background: hexToRgb(chatConfig?.color),
        color: chatConfig?.color,
      }}
    >
      {getIconByIndex(chatConfig?.userAvatar?.svgId - 1)}
    </div>
  );

  return (
    <div key={currentIndex} className="userright">
      <>
        <div
          className={` userChatBox ${
            chatConfig?.theme === "dark" ? "dark-bg" : "light-bg"
          } `}
          style={{ background: chatConfig?.color }}
        >
          <p
            style={{
              color: textColor,
              fontSize: typography?.size + "px",
              fontFamily: typography?.font?.fontFamily,
            }}
          >
            {" "}
            {typeof msg.text === "string" ? msg.text : msg.text.value}
          </p>
        </div>
        {embed ? (
          <div className="preview-thb-right">
            {Avatar}
            {/* <UserIcon fontColor={textColor} bgColor={chatConfig?.color} />{" "} */}
          </div>
        ) : (
          <div
            className={`Previewthumbnail ${
              chatConfig?.theme === "dark" ? "dark-bg" : "light-bg"
            } `}
          >
            {Avatar}
          </div>
        )}
      </>
    </div>
  );
}

